@import url(https://fonts.googleapis.com/css?family=Montserrat|Montserrat:bold);

html {
	height: 100%;
}
body {
	height: 100%;
	background-image: linear-gradient(to top, magenta , orange);
	overflow: hidden;
}
h1 {
	cursor: default;
	font-size: 2em;
}
h2 {
	cursor: inherit;
}
#mainbox {
	text-align: center;
	margin: 4% 8%;
}
#wrapperbox {
	background: #fff;
	border-radius: 10px;
	overflow: hidden;
 	justify-content: center;
	height: 600px;
 	box-shadow: 0px 13px 37px -8px rgba(0,0,0,0.6);
}
#loginbox {
	font-family: 'Montserrat', sans-serif;
	width: 100%;
	height: 60%;
}
.loginError {
	font-size:85%;
}
#loginError {
	color:red;
}
#plannerError {
	 color:transparent;
 }
#guestError {
	color:transparent;
}
.infoBox {
	margin: 5%;
	color: #333333;
  	background: transparent;
}
.textBox {
	font-family: 'Montserrat', sans-serif;
	font-size: 100%;
	border: none;
	border-bottom: 2px solid rgba(105, 105, 105, 0.3);
	transition: 1s;
  width: 50%;
 	height: 50px;
  background: transparent;
	animation-name: fadeInRight;
  padding-left: 5%;
}
.textBox:hover {
	border-bottom: 2px solid rgba(105, 105, 105, 0.8);
}
.textBox:focus {
	border-bottom: 2px solid rgba(105, 105, 105, 0.8);
	outline: none;
}
.login {
	width: 100%;
	height:100%;
	content:"";
	display:table;
	clear:both;
}
#guestLogin {
	margin-top: 4%;
	float:left;
	width:49.5%;
	height:100%;
}
#plannerLogin {
	margin-top: 4%;
	float:left;
	width:49.5%;
	height:100%;
	border-right:thin solid grey;
}
#newRegister {
	cursor: pointer;
	width:50%;
	margin: 0 25% 0;
	color:rgb(26, 13, 171);
}
#newRegister:hover {
	text-decoration: underline;
}
#newRegister:active {
	font-weight: bold;
}
#registerBox {
	font-family: 'Montserrat', sans-serif;
	height: 100%;
	margin-top: 10%;
}
#inputs {
	margin: 10% 0;
}
.button {
	font-family: 'Montserrat', sans-serif;
	color: white;
	font-weight: bold;
	font-size: 100%;
	width: 50%;
	margin: 4% 4% 4%;
	padding: 2.5%;
	background-image: linear-gradient(to right, magenta , orange);
	box-shadow: 0px 8px 15px -4px rgba(0,0,0,0.59);
	border-radius: 100px;
	border-color: transparent;
	cursor: pointer;
}
.button:hover {
	background-image: linear-gradient(to right, orange , magenta);
}
.button:active {
	background-image: linear-gradient(to right, orange , magenta);
	border: 1px solid black;
}
#guestButtons {
	width: 40%;
	font-size: 80%;
}
#guestBoxInfo {
	float: left;
	width: 50%;
	border-right: thin solid grey;
}
#guestBoxGroups {
	margin-top: 30%;
}
#formGuest label {
	display: block;
	float: left;
	margin-left: 25%;
	margin-right: 40%;
}
#formGuest input {
	margin-right: 1%;
}
.guestItem {
	float: left;
	margin-top: 3%;
	margin-bottom: 3%;
	margin-left: 10%;
	list-style-type: none;
  padding: 0;
}
#guestItemOpen {
	font-size: 100%;
	margin-right: 7%;
	float: right;
	border: none;
	cursor: pointer;
	margin-top: 3%;
	margin-bottom: 3%;
}
#guestItemOpen:hover {
	font-weight: bold;
}
#guestItemOpen:focus {
	font-weight: bold;
	outline: none;
}
#guestList {
	font-family: 'Montserrat', sans-serif;
	padding: 0;
	list-style-type: none;
	margin: 0;
}
#welcome {
	display: flex;
	background-image: linear-gradient(to right, orange , magenta);
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	font-size: 100%;
	color: #333333;
	justify-content:space-around;
}
.userMain {
	padding: 0 25%;
}
#dialogbox {
	font-family: 'Montserrat', sans-serif;
	display: none;
}
.dialogbox2 {
	font-family: 'Montserrat', sans-serif;
	display: none;
}
dialog {
	z-index: 10;
	background: white;
	width: 50%;
	border: 0;
	border-radius: 10px;
	position: fixed;
  top: 50%;
	left: 50%;
  transform: translate(-50%, -50%);
	box-shadow: 0px 13px 37px -8px rgba(0,0,0,0.6);
}
#closeWindow {
	position: absolute;
	margin: 1% 0 0 1%;
}
#closeButton {
	font-family: 'Montserrat', sans-serif;
	font-size: 100%;
	cursor: pointer;
	background: transparent;
	border: none;
}
#closeButton:hover {
	font-weight: bold;
}
#closeButton:focus {
	font-weight: bold;
	outline: none;
}
#listWrapper {
	width:100%;
	top: 40%;
	left: 50%;
	max-height: 80%;
	background: white;
	overflow: auto;
}
#eventList {
	list-style-type:none;
	padding:0 5%;
}
.eventItem {
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	font-size: 115%;
	color: #333333;
	cursor: pointer;
	margin:2%;
	padding: 4%;
	box-shadow: 0px 10px 25px 3px rgba(0,0,0,0.3);
	border-radius: 20px;
}
.eventItem:active {
	border: thick solid black;
}
#logout {
	width:10%;
	height:10%;
	position: relative;
	margin: 1em;
	padding:1% 1% 1% 1%;
}
#navigation {
	width:15%;
	height:10%;
	position: relative;
	margin: 1em;
	padding:1% 1% 1% 1%;
}
#sidebar {
	height: 60%;
	width: 250px;
	float: right;
	position: relative;
	overflow: hidden;
	z-index: 1;
	right: 0;
	display: flex;
	flex-direction: column;
}
#sidebar #search {
	border-left: 1px solid black;
	border-bottom: 1px solid black;
	padding-bottom: 4%;
	overflow: hidden;
}
#sidebar #searchBox {
	border: none;
}
#sidebar #list {
	width: 100%;
	height: 100%;
	padding: 0;
	list-style-type: none;
	margin: 0;
	overflow-x: hidden;
	border-left: 1px solid black;
}
#sidebar #add {
	overflow: hidden;
}
#sidebar #add_guest {
	height: 50px;
	box-shadow: none;
	width: 100%;
	margin: 0;
	padding-bottom: 8%;
	border-radius: 0;
}
#seatingChart {
	margin-top: 4%;
	float:left;
	width:49.5%;
	height:100%;
	border-right:thin solid grey;
}
#guestInfo {
	margin-top: 4%;
	float:left;
	width:49.5%;
	height:100%;
}
